@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,400,500,600&display=swap);
/* FONTS */
@font-face {
    font-family: 'icomoon';
    src: url(/reliability-score-frontend/static/media/icomoon.1a31d18c.eot);
    src: url(/reliability-score-frontend/static/media/icomoon.1a31d18c.eot#iefix) format('embedded-opentype'), 
        url(/reliability-score-frontend/static/media/icomoon.789d3413.ttf) format('truetype'), 
        url(/reliability-score-frontend/static/media/icomoon.c457a26b.woff) format('woff'), 
        url(/reliability-score-frontend/static/media/icomoon.f8978700.svg#icomoon) format('svg');
    font-weight: normal;
    font-style: normal;
}

html, body {
    margin: 0;
    padding: 0;
    background: #fcfcff;
}

.content {
    margin: auto;
    font-family: 'Work Sans', sans-serif;
}

.content.global {
    padding-top:80px;
}

.shadow {
    box-shadow: 0 3px 10px 0 #0000001f;
    background: #fff;
    border-radius: 5px;
}

@media only screen and (min-width: 1000px) {
    .content {
        width: 1000px;
    }
}

@media only screen and (max-width: 999px) {
    .content {
        width: 95%;
    }
}

h1, h2, h3 {
    color: #025aa0;
}

@media only screen and (min-width: 1000px) {
    .inside-content {
        padding: 10px;
    }
}

@media only screen and (max-width: 999px) {
    .inside-content {
        padding: 5px;
    }
}
header {
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 10px;
    overflow: hidden;
    position: fixed;
    width:100%;
    z-index: 900;
}

header .header-back-btn {
    float: left;
    margin-top: 5px;
    padding-right: 10px;
    margin-right: 10px;
    border-right: 1px solid #E2E8ED;
    cursor: pointer;
}

header .logo {
    float: left;
    overflow: hidden;
}

header .logo img {
    width: 40px;
    height: 40px;
    float: left;
    margin-right: 10px;
}

header .logo h1 {
    float: left;
    margin: 0;
    padding: 0;
    font-size: 20px;
    margin-top: 5px;
}

header .header-back-btn:hover .icon-arrow-left:before {
    color: #025aa0;
}

.icon-arrow-left:before {
    content: "\e901";
    font-family: 'icomoon';
    font-size: 20px;
    color: #bdc3c7;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}
/* Fade effect */

.fade-enter {
    opacity: 0;
}

.fade-enter-active {
    opacity: 1;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

.fade-exit-done {
    opacity: 0;
}

.fade-appear {
    opacity: 0;
}

.fade-appear-active {
    opacity: 1;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
}

/* Slide effect */

.slide-enter {
    width: 0px;
    overflow: hidden;
    float: left;
}

.slide-enter-active {
    width: 40px;
    -webkit-transition: all 100ms linear;
    transition: all 100ms linear;
    overflow: hidden;
    float: left;
}

.slide-exit {
    width: 40px;
    overflow: hidden;
    float: left;
}

.slide-exit-active {
    width: 0px;
    -webkit-transition: all 100ms linear;
    transition: all 100ms linear;
    overflow: hidden;
    float: left;
}

.slide-exit-done {
    width: 0px;
    overflow: hidden;
    float: left;
}

.slide-appear {
    width: 0px;
    overflow: hidden;
    float: left;
}

.slide-appear-active {
    width: 100px;
    -webkit-transition: all 100ms linear;
    transition: all 100ms linear;
    overflow: hidden;
    float: left;
}
.content.search-panel h2:first-of-type {
    font-weight: 600;
    font-size: 30px;
}

.content.search-panel form span.title-form {
    font-size: 14px;
    color: #025aa0;
    font-weight: 500;
    display: block;
    margin-bottom: 5px;
    margin-top: 15px;
}

.content.search-panel form button {
    margin-top: 30px;
    width: 100%;
}

.content.search-panel .time-form {
    display: flex;
}

@media only screen and (max-width: 999px) {
    .content.search-panel .time-form {
        justify-content: space-between;
    }
}

@media only screen and (min-width: 1000px) {
    .content.search-panel .time-form .item {
        margin-right: 50px;
    }
}
.direction-collection {
    width: 100%;
}

.direction-collection .direction-item {
    margin: 10px 0;
}

.direction-collection .direction-item h2 {
    margin-top: 0;
    font-weight: 600;
    font-size: 26px;
}

.direction-collection .direction-item .details {
    background: #f8f9fe;
    padding: 0;
    border-radius: 4px;
    overflow: hidden;
    border: 1px solid #DBDCE0;
}

.direction-collection .direction-item .details .important-sign {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 50px 50px 0;
    border-color: transparent orange transparent transparent;
    margin-left: calc(100% - 50px);
    margin-bottom: -40px;
}

.direction-collection .direction-item .details .important-sign img {
    width: 20px;
    height: 20px;
    margin-left: 26px;
    margin-top: 6px;
}

.ant-steps-item-process>.ant-steps-item-tail::after {
    background: #1890ff;
}

.direction-collection .direction-item .details .ant-timeline-item-content h3 {
    font-size: 20px;
}

.direction-collection .direction-item .details .station-informations div {
    display: flex;
}

@media only screen and (max-width: 999px) {
    .direction-collection .direction-item .details .station-informations div {
        justify-content: space-between;
    }
}

@media only screen and (min-width: 1000px) {
    .direction-collection .direction-item .details .station-informations div p {
        margin-right: 20px;
    }
    .direction-collection .direction-item .details .station-informations div p:last-of-type {
        margin-right: 0px;
    }
}

.direction-collection .direction-item .details .station-informations div.to {
    margin-top: 15px;
}

.direction-collection .direction-item .details .station-informations p {
    font-size: 15px;
}

.ant-timeline-item-head {
    width: 20px;
    height: 20px;
}

.ant-timeline-item-content {
    margin-left: 30px;
}

.ant-timeline-item-tail {
    left: 9px;
}

.ant-timeline-item {
    padding: 0;
}

.direction-collection .direction-item .details .last-direction-notice {
    color: orange;
    overflow: hidden;
    cursor: pointer;
}

.direction-collection .direction-item .details .last-direction-notice img {
    width: 20px;
    height: 20px;
    float: left;
    margin-right: 10px;
}

@media only screen and (max-width: 999px) {
    .direction-collection .direction-item .details .last-direction-notice img {
        margin-top: 5px;
    }
}

.direction-collection .direction-item .details .last-direction-notice p {
    float: left;
    width: calc(100% - 30px);
    margin: 0;
    padding: 0;
}

.last-direction-notice {
    margin: 1.75%;
    padding: 1%;
    border-style: solid;
    border-width: 1px;
    border-radius: 10px;
    overflow: hidden;
}

.last-direction-notice:hover {
    color: white;
    background-color: orange;
}

.last-direction-notice:hover p {
    color: white;
}

.last-direction-notice-icon {
    width: 20px;
    height: 20px;
    background-image: url(data:image/png;base64,);
    background-size: 20px;
    float: left;
    margin-right: 10px;
}

.last-direction-notice:hover .last-direction-notice-icon {
    background-image: url(data:image/png;base64,);
}

.graph-container {
    display: flex;
    flex-direction: row;     /* make main axis horizontal (default setting) */
    justify-content: center; /* center items horizontally, in this case */
    align-items: center;     /* center items vertically, in this case */
}

